@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../..//fonts/Roboto-Medium.woff2") format("woff2"), url("../../fonts/Roboto-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/Roboto-Condensed-Regular.woff2") format("woff2"), url("../../fonts/Roboto-Condensed-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/Roboto-Condensed-Bold.woff2") format("woff2"), url("../../fonts/Roboto-Condensed-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/pt-mono-regular.woff2") format("woff2"), url("../../fonts/pt-mono-regular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  scroll-behavior: smooth;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: blue;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.logo {
  display: flex;
  align-items: center;
  margin-right: 160px;
  width: 215px;
}

.logo__image {
  margin-right: 15px;
}

.logo__text {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: #f2f5f7;
  margin: 5px 0 0;
}

.logo__description {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: rgba(255, 255, 255, 0.7);
  margin: 0;
}

.header-contacts {
  margin-right: 165px;
}

.header-contacts__wrapper {
  display: flex;
  justify-content: space-between;
  width: 460px;
}

.header-contacts__image {
  margin-right: 15px;
}

.header-contacts__link {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f7;
  display: flex;
  align-items: center;
}

.header {
  width: 100%;
  height: 120px;
  background: linear-gradient(#041827 84.9%, rgba(4, 24, 39, 0) 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 27px;
  position: fixed;
  z-index: 2;
}

.header__container {
  width: 1920px;
  display: flex;
  align-items: center;
  padding-left: 350px;
}

.header__button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f7;
  background-color: transparent;
  padding: 10px 15px;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
  cursor: pointer;
  width: 160px;
}

.header__button:hover, .header__button:focus {
  color: #041827;
  background-color: #f2f5f7;
}

.title {
  width: 100%;
  min-height: 880px;
  background-color: #000000;
  background-image: url("../../img/main-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title__container {
  width: 1920px;
  min-height: 100%;
  padding: 301px 80px 80px 475px;
}

.title__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.085em;
  text-transform: uppercase;
  color: #f2f5f7;
  margin: 0;
  margin-bottom: 36px;
}

.title__text {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 155%;
  color: #f2f5f7;
  margin: 0;
  margin-bottom: 62px;
}

.title__button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.065em;
  text-transform: uppercase;
  color: #f2f5f7;
  padding: 15px 25px;
  background-color: #00b2ff;
  border-radius: 3px;
}

.title__button:hover, .title__button:focus {
  background-color: #0072a3;
}

.advantages {
  width: 100%;
  min-height: 864px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantages__container {
  width: 1920px;
  min-height: 100%;
  padding: 104px 350px 98px;
}

.advantages__list {
  list-style-type: none;
  padding: 0;
  display: grid;
  grid-template-columns: 595px 595px;
  column-gap: 30px;
  row-gap: 30px;
}

.advantages-card {
  width: 595px;
  height: 300px;
  background-color: #ffffff;
  border: 1px solid #ebebeb;
  padding: 40px 45px;
  position: relative;
}

.advantages-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: -1px;
  width: 5px;
  height: 299px;
  background-color: #00b2ff;
}

.advantages-card__image {
  margin-bottom: 14px;
  fill: #00b2ff;
}

.advantages-card__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff;
  margin: 0;
  margin-bottom: 15px;
}

.advantages-card__text {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  color: #041827;
  margin: 0;
}

.advantages-card:hover,
.advantages-card:focus {
  background-color: #00b2ff;
}

.advantages-card:hover .advantages-card__title,
.advantages-card:focus .advantages-card__title {
  color: #ffffff;
}

.advantages-card:hover .advantages-card__text,
.advantages-card:focus .advantages-card__text {
  color: #ffffff;
}

.advantages-card:hover .advantages-card__image,
.advantages-card:focus .advantages-card__image {
  fill: #ffffff;
}

.about-company {
  width: 100%;
  min-height: 420px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-company__container {
  width: 1920px;
  min-height: 100%;
  padding: 5px 350px 120px;
  position: relative;
}

.about-company__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #041827;
  margin: 0;
  margin-bottom: 13px;
  margin-left: 250px;
}

.about-company__text-container {
  margin-left: 250px;
  overflow-y: hidden;
  margin-bottom: 21px;
}

.about-company__text-container--close {
  height: 170px;
}

.about-company__text-container--open {
  height: auto;
}

.about-company__text {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #041827;
  margin: 0;
  margin-bottom: 20px;
}

.about-company__button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #00b2ff;
  padding: 14px 12px;
  border: 1px solid #00b2ff;
  border-radius: 3px;
  background-color: transparent;
  margin-left: 250px;
  width: 180px;
  cursor: pointer;
}

.about-company__button:hover {
  color: #ffffff;
  background-color: #00b2ff;
}

.about-company__image {
  position: absolute;
  top: 6px;
  left: 350px;
}

.goods {
  width: 100%;
  min-height: 960px;
  background-color: #041827;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.goods__container {
  width: 1920px;
  min-height: 100%;
  padding: 66px 350px 125px;
}

.goods__text {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #ffffff;
  margin: 0;
  margin-bottom: 53px;
}

.goods__list {
  list-style-type: none;
  padding: 0;
  display: grid;
  grid-template-columns: 350px 350px 350px;
  column-gap: 85px;
}

.goods-card {
  width: 350px;
  height: 650px;
  background-image: url("../../img/goods/card1.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
}

.goods-card::before {
  content: "";
  position: absolute;
  width: 60px;
  height: 60px;
  left: 289px;
  top: 590px;
  background-color: #00b2ff;
  border-radius: 3px 0px 0px 0px;
  background-image: url("../../img/arrow.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.goods-card--1 {
  background-image: url("../../img/goods/card1.png");
}

.goods-card--2 {
  background-image: url("../../img/goods/card2.png");
}

.goods-card--3 {
  background-image: url("../../img/goods/card3.png");
}

.goods-card__link {
  display: block;
  width: 350px;
  height: 650px;
  padding: 48px 40px 80px;
  background-color: rgba(4, 24, 39, 0.5);
}

.goods-card__link:hover, .goods-card__link:focus {
  background-color: rgba(4, 24, 39, 0);
}

.goods-card__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: #ffffff;
  margin: 0;
}

.contact-form {
  width: 100%;
  height: 840px;
  background-color: #00b2ff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-form__container {
  width: 1920px;
  min-height: 100%;
  padding: 165px 350px 260px 975px;
  position: relative;
}

.contact-form__container::before {
  content: "";
  position: absolute;
  top: 0;
  right: 100%;
  width: 100%;
  height: 840px;
  background-color: #026592;
}

.contact-form__container::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  border-top: 840px solid #026592;
  border-right: 972px solid transparent;
}

.contact-form__image {
  position: absolute;
  top: 246px;
  left: 350px;
  z-index: 1;
}

.contact-form__input-container {
  display: grid;
  grid-template-columns: 275px 275px;
  column-gap: 30px;
  row-gap: 20px;
  margin-bottom: 34px;
  margin-left: 5px;
}

.contact-form__confirm-container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  margin-left: 5px;
}

.contact-form__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #ffffff;
  margin: 0;
  margin-bottom: 12px;
}

.contact-form__text {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  margin: 0;
  margin-bottom: 35px;
  margin-left: 5px;
}

.contact-form__name,
.contact-form__phone {
  width: 275px;
  height: 50px;
  padding: 15px;
}

.contact-form__question {
  width: 580px;
  height: 155px;
  padding: 25px 15px;
}

.contact-form__name,
.contact-form__phone,
.contact-form__question {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  background-color: #019bdf;
  border: none;
  border-radius: 3px;
}

.contact-form__name:hover,
.contact-form__phone:hover,
.contact-form__question:hover {
  border: 1px solid #ffffff;
  outline: none;
}

.contact-form__name:active,
.contact-form__phone:active,
.contact-form__question:active {
  border: 1px solid #ffffff;
  outline: none;
}

.contact-form__name::placeholder,
.contact-form__phone::placeholder,
.contact-form__question::placeholder {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}

.contact-form__agreement {
  display: none;
}

.contact-form__agreement-label {
  width: 235px;
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
  margin-left: 25px;
  position: relative;
  margin-top: 15px;
}

.contact-form__agreement-label::before {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 3px;
  top: 9px;
  left: -25px;
}

.contact-form__agreement-label::after {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  background-color: transparent;
  top: 9px;
  left: -25px;
}

.contact-form__agreement:checked + .contact-form__agreement-label::after {
  background-image: url("../../img/check-mark.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.contact-form__button {
  width: 265px;
  margin-right: 50px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  padding: 14px 25px;
  background-color: #041827;
  border-radius: 3px;
  cursor: pointer;
  border: none;
}

.contact-form__button:hover, .contact-form__button:focus {
  background-color: #026592;
  border: none;
}

.social {
  position: absolute;
  top: 122px;
  left: 360px;
}

.social__list {
  list-style-type: none;
  width: 150px;
  height: 40px;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.social__link {
  fill: #f2f5f7;
}

.social__link:hover {
  fill: #00b2ff;
}

.navigation {
  width: 470px;
  margin-right: 30px;
  padding-top: 9px;
}

.navigation__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0;
  margin-bottom: 21px;
}

.navigation__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 150px;
}

.navigation__item {
  width: 249px;
  min-height: 20px;
}

.navigation__item:not(:nth-child(4)) {
  margin-bottom: 5px;
}

.navigation__link {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.7);
}

.navigation__link:hover, .navigation__link:focus {
  color: #00b2ff;
}

.navigation__button {
  display: none;
}

.contacts {
  padding-top: 9px;
}

.contacts__container {
  display: flex;
  flex-direction: column;
}

.contacts__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0;
  margin-bottom: 24px;
}

.contacts__address,
.contacts__phone {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 10px;
  width: 345px;
}

.contacts__button {
  display: none;
}

.footer {
  width: 100%;
  height: 280px;
  background-color: #041827;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__container {
  width: 1920px;
  min-height: 100%;
  padding: 47px 350px;
  display: flex;
  align-items: flex-start;
  position: relative;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 3;
}

.modal--close {
  display: none;
}

.modal__window {
  position: absolute;
  width: 500px;
  height: 610px;
  left: 50%;
  top: 146px;
  transform: translate(-50%, 0);
  background-color: #00b2ff;
  border-radius: 3px;
  padding: 40px;
}

.modal__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
  color: #ffffff;
  margin: 0;
  margin-bottom: 15px;
}

.modal__text {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  margin: 0;
  margin-bottom: 40px;
}

.modal__input {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  background-color: #019bdf;
  border: none;
  border-radius: 3px;
  width: 420px;
  height: 50px;
  padding: 15px;
  margin-bottom: 20px;
}

.modal__textarea {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  background-color: #019bdf;
  border: none;
  border-radius: 3px;
  width: 420px;
  height: 155px;
  padding: 15px;
  margin-bottom: 20px;
}

.modal__input::placeholder,
.modal__textarea::placeholder {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}

.modal__input:hover,
.modal__textarea:hover,
.modal__input:focus,
.modal__textarea:focus {
  border: 1px solid #ffffff;
  outline: none;
}

.modal__input:active,
.modal__textarea:active {
  border: 1px solid #ffffff;
  outline: none;
}

.modal__checkbox {
  display: none;
}

.modal__label {
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
  margin-left: 34px;
  position: relative;
}

.modal__label::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -25px;
  width: 15px;
  height: 15px;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 2px;
}

.modal__checkbox:checked + .modal__label::after {
  content: "";
  position: absolute;
  top: -2px;
  left: -25px;
  width: 15px;
  height: 15px;
  background-image: url("../../img/check-mark.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.modal__form-button {
  width: 100%;
  height: 45px;
  background: #041827;
  border-radius: 3px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #FFFFFF;
  margin-bottom: 20px;
}

.modal__close-button {
  position: absolute;
  top: 15px;
  left: 445px;
  width: 40px;
  height: 40px;
  background-color: transparent;
  background-image: url("../../img/close.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
}

.modal__close-button:hover,
.modal__close-button:focus,
.modal__form-button:hover,
.modal__form-button:focus {
  border: 2px solid #ffffff;
  border-radius: 3px;
}

